import React from 'react';
import DataTable, { createTheme } from 'react-data-table-component';
import { Document, Page, pdfjs } from 'react-pdf';
import FileSaver from 'file-saver';
import Loader from "../components/Loader"
import '../assets/sass/main.scss';
import { Provider, themes, Button} from '@fluentui/react-northstar';
import { ArrowLeftIcon, DownloadIcon, ChevronStartIcon } from '@fluentui/react-icons-northstar';
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

const styles={
    mainPadding:null,
    mainSidePadding:{padding:"15px 1em"},
    mainPdf:{
        display:"flex", 
        justifyContent:"center",
        flexDirection:"column"
    },
    mainTable:{
        backgroundColor: "#fff",
        border: "1px solid #d8e2f0",
        borderRadius: "4px",
        boxShadow: "0 0 10px 0 rgba(19,29,41,.05)",
        position: "relative",
    },
    backButton:{
        cusor:"pointer"
    },
    arrow:{
        display: "inline-block",
        padding: "3px",
        cursor: "pointer"
    },
    
    download:{
        transform: "scale(1.5)",
        border: "none"
    },
    mainHeader:{
        display:"flex",
        justifyContent:"space-between"
    },
    table:{
        headCells: {
            style: {
              fontWeight:"bold"
            },
          },
          rows: {
            style: {
              '&:not(:last-of-type)': {
                borderBottomStyle: 'solid',
                borderBottomWidth: '2px',
                borderBottomColor: "#F2F2F2"
            }
    }
}
    }
}

const localMoney = (locale, currency, amount) =>new Intl.NumberFormat(locale, { style: 'currency', currency }).format(amount)

const langs ={
    en:{
        tableHeadings:{
            invoiceId:"Quote id",
            reference:"Reference",
            date:"Quote date",
            amount:"Amount",
            status:"Status"
        },
        statuses:{
            open:<div className="state-label state-label--full-width state-label--warning"><span className="state-label__text">open</span></div>,
            draft:<div className="state-label state-label--full-width state-label--warning"><span className="state-label__text">draft</span></div>,
            paid:<div className="state-label state-label--full-width state-label--paid"><span className="state-label__text">paid</span></div>,
            late:<div className="state-label state-label--full-width state-label--late"><span className="state-label__text">late</span></div>,
        },
        payLabel:(locale, currency, amount) => <span>Amount payable: {localMoney(locale, currency, amount)}</span>,
        payButton:"Approve"
    },
    nl:{
        tableHeadings:{
            invoiceId:"Offertenummer",
            reference:"Kenmerk",
            date:"Offertedatum",
            amount:"Bedrag",
            status:"Status"
        },
        statuses:{
            draft:<div className="state-label state-label--full-width state-label--warning"><span className="state-label__text">Openstaand</span></div>,
            open:<div className="state-label state-label--full-width state-label--warning"><span className="state-label__text">Verlopen</span></div>,
            paid:<div className="state-label state-label--full-width state-label--paid"><span className="state-label__text">Betaald</span></div>,
            late:<div className="state-label state-label--full-width state-label--late"><span className="state-label__text">Laat</span></div>,
        },
        payLabel:(locale, currency, amount)=> <span>Te betalen bedrag: {localMoney(locale, currency, amount)}</span>,
        payButton:"Goedkeuren"
    }
}
const downloadPdf = (ab, fileName) =>{
    var blob = new Blob([ab], {type: "application/pdf"});
    FileSaver.saveAs(blob, fileName);
}

class IndexPage extends React.Component{
 
    constructor(){
        super()
        this.state= {
            quotes:[],
            crrQuote:null,
            apiKey:null,
            adminId: null,
            crrRow: null,
            loading:true,
            numOfPages:null,
            locale:null,
            crrQuote:null
        }
        this.setActive= this.setActive.bind(this)
        this.clearActive = this.clearActive.bind(this)
        this.onDownloadMain = this.onDownloadMain.bind(this)
        this.fetchPdf = this.fetchPdf.bind(this)
        this.onDocumentLoadSuccess = this.onDocumentLoadSuccess.bind(this)
    }

    async fetchPdf(pdfId){
         const {apiKey, adminId} = this.state;
         
         const res = await fetch(
             `https://moneybirdhandlerproxy.azurewebsites.net/api/MoneyBirdProxy`,
             {
                 mode:"cors",
                 method:"POST",
                 body: JSON.stringify({
                     method:"GET",
                     path:`${adminId}/estimates/${pdfId}/download_pdf.json`,
                     key: apiKey
                 })
             }
         )
         return await res.arrayBuffer();
    }

    async setActive(r, e){
        this.setState({loading:true})
        const pdf = await this.fetchPdf(r.id)
        this.setState({ crrQuote: pdf, crrRow: r, loading:false })
    }

    onDownloadMain(){
        const {crrInvoice, crrRow} = this.state;
        downloadPdf(crrInvoice, `${crrRow.estimate_id}.pdf`)
    }
    async onDownloadRow(r)
    {
       this.setState({loading:true})
       const pdf = await this.fetchPdf(r.id);
       this.setState({loading:false })
       downloadPdf(pdf, `${r.invoice_id}.pdf`)
    }

    clearActive(){
        this.setState({crrQuote:null})
    }

    onDocumentLoadSuccess({ numPages }){
        this.setState({ numOfPages:numPages });
      }

   componentDidMount(){
        this.microsoftTeams = window? window.microsoftTeams: null;
        if(this.microsoftTeams){
            this.microsoftTeams.initialize();
            this.microsoftTeams.getContext(async ({entityId, locale}) =>{
                const {apiKey, adminId, contact:{value :contactId}} = JSON.parse(entityId);
                const res = await fetch(
                    `https://moneybirdhandlerproxy.azurewebsites.net/api/MoneyBirdProxy`,
                    {
                        mode:"cors",
                        method:"POST",
                        body: JSON.stringify({
                            method:"GET",
                            path:`${adminId}/estimates.json?filter=contact_id:${contactId}`,
                            key: apiKey
                        })
                    }
                )
                const json = await res.json();
                this.setState({
                    quotes: json,
                    adminId,
                    apiKey,
                    loading: false,
                    locale
                })
            })
        }
    }

    render(){
        const { quotes, crrQuote, loading, numOfPages, locale, crrRow} = this.state;
        if(!locale) return  <Loader loading={loading}/>

        const lang = langs[locale.split("-")[0]]
        if(!lang) return <p>Sorry, it doesnt look like we have a translation for {locale.split("-")[0]}</p>

        const columns =[
            {
                name:lang.tableHeadings.invoiceId,
                selector:"estimate_id",
                sortable: true
            },
            {
                name:lang.tableHeadings.reference,
                selector:"reference",
                sortable: true
            },
            {
                name:lang.tableHeadings.date,
                selector:"estimate_date",
                sortable: true,
                maxWidth:"15%",
                minWidth:"100px"
            },
            {
                name:lang.tableHeadings.amount,
                selector:"total_price_incl_tax_base",
                sortable: true,
                maxWidth:"12%",
                minWidth:"125px",
                right:true,
                cell: ({total_price_incl_tax, currency})=> localMoney(locale, currency, total_price_incl_tax)
            },
            {
                name:lang.tableHeadings.status,
                selector:"state",
                sortable: true,
                width: "10%",
                cell: r=>lang.statuses[r.state]
            },
            {
                name: "Download",
                maxWidth:"2%",
                center:true,
                ignoreRowClick:true,
                cell: r=><DownloadIcon style={{width:"32px"}} onClick={()=>this.onDownloadRow(r)}/>
            }
           
        ]
        return (
            
            <Provider theme={themes.teams}>
                {
                    crrQuote ? 
                    <div style={styles.mainPdf}>
                        <div style={{margin:"auto", width:"595px", marginBottom: "15px", display:'flex', justifyContent:"space-between"}}>
                            <div>
                                <Button primary circular icon={{as:ChevronStartIcon}} style={{fill:"white"}} onClick={this.clearActive}/>
                                <Button primary circular icon={{as:DownloadIcon}} style={{fill:"white", marginLeft: "5px"}} onClick={this.onDownloadMain}/>
                            </div>
                            {crrRow.state!= "paid"?<div style={styles.mainPdf}>
                                <a href={crrRow.url} target="_blank" style={{textAlign:"center"}}><Button size="large" style={{backgroundColor:"#38B8A0", color:"white"}} content={lang.payButton} /></a>
                            </div>:null}
                            <div>
                                {lang.statuses[crrRow.state]}
                            </div>
                        </div>
                        <div style={{margin:"auto"}}>
                            <Document file={crrQuote} onLoadSuccess={this.onDocumentLoadSuccess}>
                            {
                                Array.from(
                                new Array(numOfPages),
                                (el, index) => (
                                    <Page
                                    key={`page_${index + 1}`}
                                    pageNumber={index + 1}
                                    />
                                ),
                                )
                            }
                        </Document>
                        </div>
                    </div>
                    :
                    quotes.length > 0?
                    <div style={styles.mainPadding}>
                        <div style={styles.mainTable}>
                            <Loader loading={loading} style={{zIndex: 100}} />
                            <DataTable
                                columns={columns}
                                data={quotes? quotes:[]}
                                onRowClicked={this.setActive}
                                pointerOnHover={true}
                                highlightOnHover={true}
                                noHeader={true}
                                customStyles={styles.table}
                            />
                        </div>
                    </div>
                    :
                    null
                }

            </Provider>
        )
    }
  
}

export default IndexPage;
